import React, { useState } from "react";
import styled from "styled-components";
import { useAppDispatch, useAppSelector } from "store/hooks";
import { appState } from "store/app/selectors";
import List from "components/Molecules/List/List";
import Container from "components/Container/Container";
import SearchBar from "components/Molecules/SearchBar/SearchBar";
import { setFiltered } from "store/app/slice";
import Search from "components/Molecules/Search/Search";

const TVSeries: React.FC = () => {
  const { tvSeries } = useAppSelector(appState);
  const [searchText, setSearchText] = useState("");
  const dispatch = useAppDispatch();
  const onSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearchText(e.target.value);
    dispatch(setFiltered({ name: "TV_SERIES", value: e.target.value }));
  };

  return (
    <Container>
      <SearchBar placeholder='Search for TV Series' onChange={onSearch} />
      {searchText.length > 0 ? (
        <Search value={searchText} data={tvSeries} />
      ) : (
        <TVSeriesSection>
          <Title>TV Series</Title>
          <List movies={tvSeries} />
        </TVSeriesSection>
      )}
    </Container>
  );
};

const TVSeriesSection = styled.section`
  margin: 2.4rem 1.2rem;
`;

const Title = styled.h3`
  color: white;
  font-size: 32px;
  font-weight: 100;
`;

export default TVSeries;
