import React, { useState } from "react";
import Container from "components/Container/Container";
import styled from "styled-components";
import SearchBar from "components/Molecules/SearchBar/SearchBar";
import List from "components/Molecules/List/List";
import { useSelector } from "react-redux";
import { appState } from "store/app/selectors";
import { useAppDispatch } from "store/hooks";
import { setFiltered } from "store/app/slice";
import Search from "components/Molecules/Search/Search";

const BookMarked: React.FC = () => {
  const { bookmarkedMovies, bookmarkedTVSeries, search } =
    useSelector(appState);
  const [searchText, setSearchText] = useState("");
  const dispatch = useAppDispatch();
  const onSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearchText(e.target.value);
    dispatch(setFiltered({ name: "BOOKMARKED", value: e.target.value }));
  };
  return (
    <Container>
      <SearchBar
        placeholder='Search for bookmarked shows'
        onChange={onSearch}
      />
      {searchText.length > 0 ? (
        <Search data={search.filtered} value={searchText} />
      ) : (
        <>
          <BookMarkedSection>
            <Title>Bookmarked Movies</Title>
            <List movies={bookmarkedMovies} />
          </BookMarkedSection>
          <BookMarkedSection>
            <Title>Bookmarked TV Series</Title>
            <List movies={bookmarkedTVSeries} />
          </BookMarkedSection>
        </>
      )}
    </Container>
  );
};

const BookMarkedSection = styled.section`
  margin: 2.4rem 1.2rem;
`;

const Title = styled.h3`
  color: white;
  font-size: 32px;
  font-weight: 100;
`;

export default BookMarked;
