import React from "react";
import styled from "styled-components";
import device from "theme/breakpoints";
import Movies from "types/movies";
import Item from "../Item/Item";

type ListProps = {
  movies: Movies[];
};

const List: React.FC<ListProps> = ({ movies }) => {
  return (
    <ListContainer>
      {movies
        .filter((el) => !el.isTrending)
        .map((item, index) => (
          <Item item={item} key={index} />
        ))}
    </ListContainer>
  );
};

const ListContainer = styled.div`
  display: grid;
  grid-template-columns: repeat(5, 0fr);
  grid-template-rows: repeat(2, 0fr);
  grid-column-gap: 6px;
  grid-row-gap: 20px;
  margin: 20px 0;
  @media only screen and ${device.mobileS} and ${device.tabletMax} {
    grid-template-columns: repeat(3, 0fr);
  }
  @media ${device.laptop} and ${device.laptopLMax} {
    grid-template-columns: repeat(4, 0fr);
  }
  @media only screen and ${device.mobileS} and ${device.mobileLMax} {
    grid-template-columns: repeat(2, 0fr);
  }
`;

export default List;
