import { Link, useMatch, useResolvedPath, PathMatch } from "react-router-dom";
import type { LinkProps } from "react-router-dom";
import styled from "styled-components";
import colors from "theme/colors";

export default function CustomLink({ children, to, ...props }: LinkProps) {
  const resolved = useResolvedPath(to);
  const match = useMatch({ path: resolved.pathname, end: true });

  return (
    <StyledLink match={match} to={to} {...props}>
      {children}
    </StyledLink>
  );
}

const StyledLink = styled(Link)<{ match: PathMatch<string> | null }>`
  margin: 1.2rem 0;
  text-decoration: none;
  fill: ${(p) => (p.match ? colors.$purewhite : "#5a698f")};
  stroke: ${(p) => p.match && colors.$purewhite};
  transition: all ease 0.1s;
  &:hover {
    fill: ${colors.$red};
    stroke: ${colors.$red};
  }
`;
