import { createSlice } from "@reduxjs/toolkit";
import { initialState, sliceName } from "./constants";
import { signUp, signIn, signOut } from "./thunk";
import { FirebaseError } from "firebase/app";

export const authSlice = createSlice({
  name: sliceName,
  initialState,
  reducers: {
    setError: (state, { payload }) => {
      state.error = payload;
    },
  },
  extraReducers: (builder) => {
    // SignUp
    builder.addCase(signUp.pending, (state) => {
      state.loading = "pending";
    });
    builder.addCase(signUp.rejected, (state, { payload }) => {
      if (payload instanceof FirebaseError) {
        state.error = payload;
        state.loading = "failed";
      }
    });
    builder.addCase(signUp.fulfilled, (state, action) => {
      state.user = action.payload.user;
      state.token = action.payload.token;
      state.loading = "succeeded";
    });
    // SignIn
    builder.addCase(signIn.pending, (state) => {
      state.loading = "pending";
    });
    builder.addCase(signIn.rejected, (state, { payload }) => {
      if (payload instanceof FirebaseError) {
        console.log(payload);
        state.error = payload;
        state.loading = "failed";
      }
    });
    builder.addCase(signIn.fulfilled, (state, action) => {
      if (action.payload && !(action.payload instanceof FirebaseError)) {
        state.user = action.payload.user;
        state.token = action.payload.token;
        state.loading = "succeeded";
      } else if (action.payload) {
        state.loading = "failed";
        state.error = action.payload;
      }
    });
    // SignOut
    builder.addCase(signOut.pending, (state) => {
      state.loading = "pending";
    });
    builder.addCase(signOut.rejected, (state, { payload }) => {
      if (payload instanceof FirebaseError) {
        state.error = payload;
        state.loading = "failed";
      }
    });
    builder.addCase(signOut.fulfilled, (state, action) => {
      state.user = action.payload.user;
      state.token = action.payload.token;
      state.loading = "succeeded";
    });
  },
});

export const { setError } = authSlice.actions;

export default authSlice.reducer;
