import React from "react";
import colors from "theme/colors";
import styled from "styled-components";
import { useDispatch } from "react-redux";
import { signOut } from "store/auth/thunk";
import CustomLink from "components/Atoms/CustomLink/CustomLink";
import { ReactComponent as Logo } from "assets/icons/logo.svg";
import { ReactComponent as Home } from "assets/icons/icon-nav-home.svg";
import { ReactComponent as Movies } from "assets/icons/icon-nav-movies.svg";
import { ReactComponent as TVSeries } from "assets/icons/icon-nav-tv-series.svg";
import { ReactComponent as BookMarked } from "assets/icons/icon-nav-bookmark.svg";
import { ReactComponent as Avatar } from "assets/icons/Oval.svg";
import device from "theme/breakpoints";

const Sidebar: React.FC = () => {
  const dispatch = useDispatch();

  const logOutUser = (e: React.MouseEvent) => {
    e.preventDefault();
    dispatch(signOut());
  };

  return (
    <SidebarWrapper>
      <HomeIcon>
        <Logo />
      </HomeIcon>
      <Links>
        <CustomLink to='/home'>
          <Home />
        </CustomLink>
        <CustomLink to='/movies'>
          <Movies />
        </CustomLink>
        <CustomLink to='/tv-series'>
          <TVSeries />
        </CustomLink>
        <CustomLink to='/bookmarked'>
          <BookMarked />
        </CustomLink>
      </Links>
      <AvatarIcon onClick={logOutUser} />
    </SidebarWrapper>
  );
};

const SidebarWrapper = styled.aside`
  flex: 1 0 5%;
  height: 960px;
  background: ${colors.$semidarkblue};
  border-radius: 20px;
  border: none;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  margin: 10px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  @media only screen and ${device.tablet} and ${device.laptopMax} {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 65px;
    flex: initial;
    padding: 0 10px 0px 10px;
    border-radius: 10px;
  }
  @media only screen and ${device.mobileS} and ${device.mobileLMax} {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 65px;
    flex: initial;
    padding: 0 10px 0px 10px;
    border-radius: 0px;
    margin: 0px;
  }
`;

const HomeIcon = styled.div`
  margin-top: 2rem;
  flex: 0.15;
  @media only screen and ${device.mobileS} and ${device.laptopMax} {
    margin-top: 0px;
    flex: initial;
    margin-left: 0.8rem;
  }
`;

const Links = styled.div`
  display: flex;
  flex-direction: column;
  flex-grow: 0.8;
  @media only screen and ${device.mobileS} and ${device.laptopMax} {
    display: flex;
    flex-direction: row;
    flex-grow: initial;
    a {
      margin: 0 0.8rem;
    }
  }
`;
const AvatarIcon = styled(Avatar)`
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: thin solid ${colors.$purewhite};
  cursor: pointer;
  @media only screen and ${device.mobileS} and ${device.laptopMax} {
    width: 32px;
    height: 32px;
    margin-right: 0.8rem;
  }
`;

export default Sidebar;
