import { createSlice } from "@reduxjs/toolkit";
import { initialState, sliceName } from "./constants";

export const appSlice = createSlice({
  name: sliceName,
  initialState,
  reducers: {
    setFiltered: (state, action) => {
      switch (action.payload.name) {
        case "MOVIES":
          if (action.payload.value.length > 0) {
            state.movies = state.movies.filter((el, i) => {
              return el.title
                .toLowerCase()
                .includes(action.payload.value.toLowerCase());
            });
          } else {
            state.movies = state.search.initial.filter(
              (el) => el.category === "Movie"
            );
          }
          break;
        case "TRENDING":
          if (action.payload.value.length > 0) {
            state.search.filtered = state.search.filtered.filter((el, i) => {
              return el.title
                .toLowerCase()
                .includes(action.payload.value.toLowerCase());
            });
          } else {
            state.search.filtered = state.search.initial;
          }
          break;
        case "BOOKMARKED":
          if (action.payload.value.length > 0) {
            state.search.filtered = state.search.filtered.filter((el, i) => {
              if (el.isBookmarked) {
                return el.title
                  .toLowerCase()
                  .includes(action.payload.value.toLowerCase());
              }
            });
          } else {
            state.search.filtered = state.search.initial;
          }
          break;
        case "TV_SERIES":
          if (action.payload.value.length > 0) {
            state.tvSeries = state.tvSeries.filter((el, i) => {
              return el.title
                .toLowerCase()
                .includes(action.payload.value.toLowerCase());
            });
          } else {
            state.tvSeries = state.search.initial.filter((el) =>
              el.category.includes("TV Series")
            );
          }
          break;
        default:
          state.search.filtered = state.search.initial;
          break;
      }
    },
  },
});

export const { setFiltered } = appSlice.actions;
export default appSlice.reducer;
