import React, { useState } from "react";
import { useAppDispatch, useAppSelector } from "store/hooks";
import styled from "styled-components";
import SearchBar from "components/Molecules/SearchBar/SearchBar";
import Container from "components/Container/Container";
import List from "components/Molecules/List/List";
import TrendingList from "components/Molecules/List/TrendingList";
import { appState } from "store/app/selectors";
import { setFiltered } from "store/app/slice";
import Search from "components/Molecules/Search/Search";

const Home: React.FC = () => {
  const { trending, recommended, search } = useAppSelector(appState);
  const [searchText, setSearchText] = useState("");
  const dispatch = useAppDispatch();

  const onSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearchText(e.target.value);
    dispatch(setFiltered({ name: "TRENDING", value: searchText }));
  };

  return (
    <Container>
      <SearchBar
        placeholder='Search for movies or TV series'
        onChange={onSearch}
      />
      {searchText.length > 0 ? (
        <Search value={searchText} data={search.filtered} />
      ) : (
        <>
          <TrendingSection>
            <SectionTitle>Trending</SectionTitle>
            <TrendingList data={trending} />
          </TrendingSection>
          <RecommendedSection>
            <SectionTitle>Recommended for you</SectionTitle>
            <List movies={recommended} />
          </RecommendedSection>
        </>
      )}
    </Container>
  );
};

const TrendingSection = styled.section`
  margin: 1.2rem 1.2rem;
`;

const SectionTitle = styled.h3`
  color: white;
  font-size: 32px;
  font-weight: 100;
`;

const RecommendedSection = styled.section`
  margin: 2.4rem 1.2rem;
`;

export default Home;
