import Movies from "types/movies";
import data from "mocks/data.json";
import { filterToMap } from "helpers/filterToMap";

export type App = {
  movies: Movies[];
  trending: Movies[];
  recommended: Movies[];
  tvSeries: Movies[];
  bookmarkedMovies: Movies[];
  bookmarkedTVSeries: Movies[];
  search: {
    initial: Movies[];
    filtered: Movies[];
  };
};

export const initialState = {
  movies: filterToMap("Movie"),
  trending: filterToMap("", { condition: "isTrending", bool: true }),
  recommended: filterToMap("", { condition: "isRecommended", bool: false }),
  tvSeries: filterToMap("TV Series"),
  bookmarkedMovies: filterToMap("Movie", {
    condition: "isBookmarked",
    bool: true,
  }),
  bookmarkedTVSeries: filterToMap("TV Series", {
    condition: "isBookmarked",
    bool: true,
  }),
  search: {
    initial: data,
    filtered: data,
  },
} as App;

export const sliceName = "app";
