import React, { useEffect } from "react";
import Button from "components/Atoms/Button/Button";
import Input from "components/Atoms/Input/Input";
import Header from "components/Molecules/Header/Header";
import styled from "styled-components";
import colors from "theme/colors";
import { useFormik } from "formik";
import * as Yup from "yup";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { authState } from "store/auth/selectors";
import { useAppDispatch } from "store/hooks";
import { signIn } from "store/auth/thunk";
import { ToastContainer, toast } from "react-toastify";
import { setError } from "store/auth/slice";

const SignIn: React.FC = () => {
  const navigation = useNavigate();
  const { token, error } = useSelector(authState);
  const dispatch = useAppDispatch();

  const formik = useFormik({
    initialValues: {
      email: "",
      password: "",
    },
    validationSchema: Yup.object({
      email: Yup.string()
        .email("Invalid email address")
        .required("Can't be empty"),
      password: Yup.string().min(4).required("Can't be empty"),
    }),
    onSubmit: (values) => {
      dispatch(signIn(values));
    },
  });
  useEffect(() => {
    if (token) navigation("/home");
  }, [token, navigation]);

  useEffect(() => {
    if (error)
      toast.error(error.message, {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "dark",
        onClose: () => {
          dispatch(setError(null));
        },
      });
  }, [error, dispatch]);

  return (
    <SignInContainer data-id='signin'>
      <Header />
      <SignInForm onSubmit={formik.handleSubmit}>
        <SignInFormContent>
          <SignInFormTitle>Login</SignInFormTitle>
          <Input
            name='email'
            placeholder='Email address'
            type='text'
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            value={formik.values.email}
            isValid={formik.touched.email && !formik.errors.email}
            errorMessage={
              formik.touched.email && formik.errors.email
                ? formik.errors.email
                : null
            }
          />

          <Input
            name='password'
            placeholder='Password'
            type='password'
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            value={formik.values.password}
            isValid={formik.touched.password && !formik.errors.password}
            errorMessage={
              formik.touched.password && formik.errors.password
                ? formik.errors.password
                : null
            }
          />
          <SignInButton type='submit'>Login to your account</SignInButton>
          <DontHaveAnAccount>
            Don't have an account?{" "}
            <span onClick={() => navigation("/sign-up")}>Sign Up</span>
          </DontHaveAnAccount>
        </SignInFormContent>
      </SignInForm>
      <ToastContainer />
    </SignInContainer>
  );
};

const SignInContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
`;
const SignInForm = styled.form`
  width: 400px;
  height: 400px;
  background-color: ${colors.$semidarkblue};
  border-radius: 20px;
`;
const SignInFormContent = styled.div`
  padding: 30px;
`;
const SignInButton = styled(Button)`
  margin-top: 20px;
`;
const DontHaveAnAccount = styled.div`
  margin: 20px 0px;
  text-align: center;
  color: ${colors.$purewhite};
  font-weight: lighter;
  span {
    color: ${colors.$red};
    cursor: pointer;
  }
`;
const SignInFormTitle = styled.h3`
  font-size: 32px;
  color: ${colors.$purewhite};
  font-weight: lighter;
  margin-bottom: 2.4rem;
`;

export default SignIn;
