import data from "mocks/data.json";

export function filterToMap(
  cond: string = "",
  condWithBool?: { condition: string; bool?: boolean }
) {
  if (cond.length > 0 && !condWithBool) {
    return data.filter((el) => el.category.includes(cond));
  } else if (condWithBool?.condition === "isTrending") {
    return data.filter((el) => el.isTrending === condWithBool.bool);
  } else if (condWithBool?.condition === "isRecommended") {
    return data.filter((el) => el.isTrending === condWithBool.bool);
  } else if (cond.length > 0 && condWithBool?.condition === "isBookmarked") {
    return data.filter(
      (el) =>
        el.isBookmarked === condWithBool.bool && el.category.includes(cond)
    );
  }
}
